"use client";

import { Review, User } from "@/lib/definitions";
import { Avatar, AvatarFallback, AvatarImage } from "@/components/ui/avatar";
import Rating from "@/components/ui/Rating";
import { Button } from "../ui/button";
import { MoreVertical, Edit, Trash2 } from "lucide-react";
import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuTrigger,
} from "@/components/ui/dropdown-menu";
import { useAuth } from "@/contexts/AuthContext";

interface ReviewCardProps {
  review: Review;
  user: User | null;
}

export default function ReviewCard({ review, user }: ReviewCardProps) {
  const { user: currentUser } = useAuth();

  if (!user) return null;

  return (
    <div className="flex space-x-3 sm:space-x-4">
      <Avatar className="h-8 w-8 sm:h-10 sm:w-10">
        <AvatarImage src={`https://api.dicebear.com/8.x/initials/svg?seed=${user.name}`} alt={user.name} />
        <AvatarFallback>{user.name.charAt(0)}</AvatarFallback>
      </Avatar>
      <div className="flex-1">
        <div className="flex justify-between items-start">
            <div>
                <p className="font-semibold text-sm sm:text-base">{user.name}</p>
                <div className="flex items-center gap-2">
                <Rating rating={review.rating} />
                <span className="text-xs text-muted-foreground">{new Date(review.createdAt).toLocaleDateString()}</span>
                </div>
            </div>
            {currentUser && review.userId === currentUser.id && (
                 <DropdownMenu>
                    <DropdownMenuTrigger asChild>
                        <Button variant="ghost" size="icon" className="h-8 w-8 flex-shrink-0">
                            <MoreVertical className="h-4 w-4" />
                        </Button>
                    </DropdownMenuTrigger>
                    <DropdownMenuContent align="end">
                        <DropdownMenuItem>
                            <Edit className="mr-2 h-4 w-4" />
                            <span>Edit</span>
                        </DropdownMenuItem>
                        <DropdownMenuItem className="text-destructive">
                            <Trash2 className="mr-2 h-4 w-4" />
                            <span>Delete</span>
                        </DropdownMenuItem>
                    </DropdownMenuContent>
                </DropdownMenu>
            )}
        </div>
        <p className="mt-2 text-sm text-foreground/80">{review.reviewText}</p>
      </div>
    </div>
  );
}
